<template>
  <div name="Game_nav">
    <div class="game_nav_box" @click="push_game">
      <div
        class="game_nav_box_imgdiv"
        :style="'background-image:' + 'url(' + _getImgUrl('show') + ')'"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Game_nav",
  data() {
    return {};
  },
  methods: {
    push_game() {
      this.$message.success({
        dangerouslyUseHTMLString: true,
        message:
          "<h3>  => Showing </h3><br/><br/><p></p><br/><p>Esc()</p>",
      });
      clearTimeout(this.timer); //

      this.timer = setTimeout(() => {
        //
        this.$router.push("/show");
      }, 1300);
    },
    // 
    _getImgUrl(id) {
      // return require(pic_name)
      return require("../../../public/img/ico_png/" + id + ".png");
      // return require(url);
    },
  },
  computed: {},
  created() {},
  mounted() {},
};
</script>

<style scoped >
.game_nav_box {
  padding: 5px;
  /* width: 45px;
  height: 45px; */
  position: absolute;
  bottom: 3%;
  right: 2.5%;
  /* padding: 10px; */
  /* background-color: tomato; */
  border-radius: 50%;
  /* background-color: antiquewhite; */
  border-radius: 50%;
  cursor: pointer;
  transition: ease 0.3s;
}
.game_nav_box:hover {
  background-color: rgba(235, 253, 252, 0.815);
}

.game_nav_box_imgdiv {
  /* background-color: blue; */
  width: 35px;
  height: 35px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
</style>
