<template>
  <div name="Avatar" class="pointer_" @click="login_in_qq">
    <div class="avatar_div right pointer_">
      <el-avatar :size="size" :src="avatar_is_ok_method()"></el-avatar>
    </div>
  </div>
</template>

<script>
export default {
  name: "Avatar",
  data() {
    return {
      // 
      login_in_msg: "",
      // qq
      qqInfo: {
        name: "",
        avatar: "",
      },
      tooltip_is_no: false,
      avatar_is_ok: false,
      size: 45,
    };
  },
  methods: {
    login_success() {
      this.$notify({
        duration: 1300,
        title: "QQ",
        message: ",",
        type: "success",
      });
    },
    // qq
    async login_in_qq() {
      const confirmResult = await this.$prompt("QQ", "QQ", {
        cancelButtonText: "",
        confirmButtonText: "",
        inputPattern: /[1-9][0-9]{4,14}/,
        inputErrorMessage: "QQ",
      }).catch((err) => err);
      //  , 
      if (confirmResult === "cancel")
        return this.$message.info("");
      // this.tooltip_is_no = true;
      // ,
      console.log(confirmResult.value);
      const { data: res } = await this.$http.get(
        `https://api.no0a.cn/api/qqinfo/${confirmResult.value}`
      );
      // console.log(res);
      if (res.status !== 1) return this.$message.error(",");
      this.loading = true;
      this.qqInfo.avatar = res.qqinfo.qqavatar;
      this.login_success();
      this.avatar_is_ok = true;
      this.avatar_is_ok_method();
      // ;  ,
      const {
        data: { name: name },
      } = await this.$http.get(
        `https://api.julym.com/qq/?qq=${confirmResult.value}`
      );
      this.qqInfo.name = name;
      // this.tooltip_is_no = false;
      // console.log(name);
      // console.log(this.qqInfo);
    },
    // 
    avatar_is_ok_method() {
      // ,
      if (!this.avatar_is_ok) {
        this.login_in_msg = "";
        return this._getImgUrl("me");
      }
      this.login_in_msg = ": " + this.qqInfo.name;
      return this.qqInfo.avatar;
    },
    // 
    _getImgUrl(id) {
      // return require(pic_name)
      return require("../../../public/img/ico_png/" + id + ".png");
      // return require(url);
    },
  },
  computed: {},
  async created() {
    this.avatar_is_ok_method();
  },
  async mounted() {},
};
</script>

<style scoped >
.avatar_div {
  margin: 3% 2% 0 0;
  /* float: right; */
  border-radius: 50%;
  /* box-shadow: 1px 1px 5px rgba(245, 242, 242, 0.329); */
  cursor: pointer;
}
</style>
